@import "../../../styles/colors";
@import "../../../styles/breakpoints";

.Cursor {
  pointer-events: none;

  @include medium {
    &__ball {
      position: fixed;
      top: 0;
      left: 0;
      mix-blend-mode: difference;
      z-index: 1000;
      display: flex;
      justify-content: center;
      align-items: center;

      circle {
        fill: $white;
      }
    }

    &__text {
      position: absolute;
      font-size: 8px;
      opacity: 0;
    }
  }
}
